import { useTheme } from '../hooks/useTheme';

const Theme = () => {
  const { theme, setTheme } = useTheme();

  const handleTheme = (theme: string) => {
    setTheme(theme);
  };

  return (
    <div className='theme-container'>
      {theme === 'light' || theme === null
        ? <img className='theme' onClick={() => handleTheme('dark')} src="data:image/png;base64," />
        : <img className='theme' onClick={() => handleTheme('light')} src="data:image/png;base64," />
      }
    </div>
  );
};

export default Theme;