const Profile = () => {
  const navigate = useNavigate();
  const [displayCategory, setDisplayCategory] = useState<string>('posts');
  const { userId, username, name, likedPosts } = useUserInfo();

  if (!username) {
    useEffect(() => {
      navigate('/signin');
    }, []);
    return null;
  }

  const { loading, error, data } = useQuery(GET_USER, {
    fetchPolicy: 'cache-and-network',
    variables: { singleUserId: Number(userId) },
  });
  console.log(likedPosts);
  console.log('single user', loading, error, data);
  console.log('reversed', [...data?.singleUser.userLikedPosts || []].reverse());
  console.log(data && new Date(data.singleUser.createdAt * 1000));
  return (
    <>
      <section className='user-profile-container'>
        <div className='user-profile'>
          <div className='user-profile-main-container'>
            <div style={{ width: '50px', height: '50px' }}><img style={{ width: 'inherit', height: 'inherit' }} src="data:image/png;base64," /></div>
            <div className='user-profile-main-names'>
              <div className='user-profile-name'>{name}</div>
              <div className='user-profile-username grey-text-color'>{username}</div>
            </div>
          </div>
          <div className='user-profile-buttons'>
            <div>edit profile</div>
          </div>
          <div>
            <div className='user-profile-bio'>bio</div>
            <div className='user-profile-joined grey-text-color'>joined {data && new Date(data.singleUser.createdAt * 1000).toString()}</div>
          </div>
          <div className='user-profile-posts-container'>
            <div className='user-profile-posts' onClick={() => setDisplayCategory('posts')}>
              <div className='user-profile-posts-icon'>&#9993;</div>
              <div className='user-profile-posts-text'>Posts</div>
              {displayCategory === 'posts' && <div className='user-profile-selected-category'></div>}
            </div>
            <div className='user-profile-posts' onClick={() => setDisplayCategory('likes')}>
              <div className='user-profile-posts-icon' style={{ color: '#FF006F' }}>&#x2661;</div>
              <div className='user-profile-posts-text'>Likes</div>
              {displayCategory === 'likes' && <div className='user-profile-selected-category'></div>}
            </div>
          </div>
        </div>
      </section>
      {displayCategory === 'posts' &&
        <section className='post-container'>
          {data ? data.singleUser.posts.map((post: PostType) => (
            <Post key={post.id} post={post} />
          )) : null}
        </section>
      }
      {displayCategory === 'likes' &&
        <section className='post-container'>
          {likedPosts ? [...likedPosts].reverse().map((post) => (
            <Post key={post.id} post={post as PostType} delay={true} />
          )) : null}
        </section>
      }
    </>
  );
};

export default Profile;